<template>
  <div>
    <ul>
      <li v-for="item in ledgerItems">
        <span>{{ item.category }}</span>
        <span>{{ item.amount | to-currency }}</span>
      </li>

      <li>
        <span><strong>Remaining:</strong> {{ amountRemaning | to-currency }}</span>
        <span><strong>Total:</strong> {{ amountSpent | to-currency }}</span>
      </li>
    </ul>

  </div>
</template>

<script>
  import moment from 'moment';
  import Settings from './utils/Settings';
  import payDayBills from './PayDayBills';

  export default {
    data () {
      return {
        ledgerItems: payDayBills.bills.filter(b => {
          let today = moment().startOf("day"),
              currentPayPeriod;

          if (today >= Settings.payDay2.startOf("day")) {
            currentPayPeriod = 2;
          } else {
            currentPayPeriod = 1;
          }

          return parseInt(b.payPeriod) == currentPayPeriod;
        }),
      };
    },

    computed: {
      amountRemaning () {
        return Settings.income - this.amountSpent;
      },

      amountSpent () {
        return this.ledgerItems.reduce((acc, item) => {
          return acc += item.amount;
        }, 0);
      },
    },

    mounted () {
      Settings.initiated();
    },
  };
</script>

<style lang="scss" scoped>
  ul {
    list-style: none;
    padding: 0;
    margin: 1rem auto;
    width: 60vw;
    display: flex;
    flex-flow: column nowrap;

    li {
      padding: 1rem;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
      margin: 0;
      display: flex;
      justify-content: space-between;
    }
  }
</style>



